	import back from '@/components/back'
	import noData from '@/components/noData'

	export default {
		components: {
			back,
			noData
		},
		data() {
			return {
				title: '推荐记录',
				user: this.$store.state.userInfo.Data,
				info: null,
				id: this.$route.query.id,
				drMsg: null,
			}
		},
		created() {
			this.pull()
		},
		mounted() {

		},
		beforeDestroy() {
			window.onscroll = null
		},
		methods: {
			pull() {
				this.$post('PlatFormAPI/SpecialistHosGroup/GetGoodsRecommendDetailForDoctor', {
					Id: this.id
				}).then(o => {
					this.info = o
				})
			},
			subtr(num1, num2) {
				let r1, r2, m, n;
				try {
					r1 = num1.toString().split(".")[1].length
				} catch(e) {
					r1 = 0
				}
				try {
					r2 = num2.toString().split(".")[1].length
				} catch(e) {
					r2 = 0
				}
				m = Math.pow(10, Math.max(r1, r2));
				n = (r1 >= r2) ? r1 : r2;
				return((num1 * m - num2 * m) / m).toFixed(2);
			},
			goArchives(){
				let obj = {}
				obj = {
					"Birthday" : "",
                    "PatientName" : this.info.PatientName,
                    "PatientId" : this.info.PatientId,
                    "AreaId" : null,
                    "AccountId" : this.info.AccountId || null,
                    "PatientSex" : this.info.PatientSex,
                    "AreaName" : null,
                    "Age" : this.info.PatientAge,
				}
				this.$store.dispatch('pushpatient', obj)
				this.$router.push("/archives")
			}
		}
	}



